import React from 'react';

export const Success = ({ count }) => {
  return (
    <div class='success-block'>
      <img src='/assets/success.svg' alt='Success' />
      <h3>!</h3>
      <p> {count}   .</p>
      <button
        onClick={() => window.location.reload()}
        className='send-invite-btn'
      >
        
      </button>
    </div>
  );
};
